<template>
    <div>
        <h4>board edit!</h4>
        <form action="/board/update" method="post" onsubmit='return false'>
            {{  board_id  }}
            <input type="input" v-model='board_content'>
            <button @click='updateMethod'></button>
        </form>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    data () {
        return {
            msg: ' board edit ',
            board_update_link: `${domain}/board/update`,
            board_id: '',
            board_content: '',
        }
    },
    methods: {
        post_update() {
            axios.post(this.board_update_link, {
                board_id: this.board_id,
                board_content: this.board_content,
            }).then(res => {
                // log(' update board , ', res.data)
                this.$router.push({
                    name: 'Board'
                })

            }).catch(res => {
                log(' update board ')
            })
        },
        updateMethod() {
            log('')
            this.post_update()
        }
    },
    created() {
        this.board_id = this.$route.query.board_id
    }
}
</script>

<style scoped>

</style>




