<template>
    <div>
        {{  msg  }}
            <div class="input-group">
                username: {{ username }}
                password: {{ password }}
                <h3></h3>
                <form action="/user/login" method="post" onsubmit='return false'>
                    <!-- <input hidden type="input" name="next_url" value="next_url"> -->
                    <input class="form-control" type="input" v-model='username' placeholder="">
                    <br>
                    <input class="form-control" type="input" v-model='password' placeholder="">
                    <br>
                    <button class="btn btn-info" @click='loginMethod'></button>
                </form>
                <br>
            </div>
            {{ infor }}
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data () {
        return {
            msg: '',
            api_link: `${domain}/user/login`,
            index_link: `${domain}/`,
            infor: '',
            next_url: '',
            username: '',
            password: '',
            domain: `${domain}`,
            
            
        }
    },
    methods: {
        loginMethod() {
            axios.post(this.api_link, {
                username: this.username,
                password: this.password,
            }).then(res => {
                if (res.data.redirect == false) {
                    this.infor = res.data.infor
                } else {
                    // log('')
                    const name = res.data.username
                    // this.$router.push({
                    //     name: 'main',
                    // })
                    this.$router.push({
                        path: this.next_url,
                    })
                }

            }).catch(res => {
                log('')
            })
        }
    },
    created() {
        this.next_url = this.$route.query.next_url || '/'
        // log('this.next_url:', this.next_url)
        // log('hahah')

    }
}
</script>

<style scoped>

</style>





