<template>
    <div class='main'>
        <!-- {{  msg  }} -->
        <!-- <h1>hello {{ username }} !</h1> -->
        <div class="test-main">
            <div class="nav">
                
                <span class='nav-ceil'><a @click="go('TopicIndex')"></a></span>
                <span class='nav-ceil'>
                    <a @click="go('Hotspot')"></a>
                </span>
                <span class='nav-ceil'>
                    <a @click="go('Photo')"></a>
                    
                </span>
            </div>
            <div class="down"></div>
            <div class="info">Hi Sneakers!</div>
        </div>
        <div class="below container">
            <div class="row">
                <div class="below-ceil col-md-6 col-sm-6">
                    <i class='glyphicon glyphicon-heart'></i>
                    
                </div>
                <div class="below-ceil col-md-6 col-sm-6">
                    <i class='glyphicon glyphicon-fire'></i>
                    
                </div>

            </div>
            <div class="row">
                <div class="below-ceil col-md-6 col-sm-6">
                    <i class='glyphicon glyphicon-camera'></i>
                    
                </div>
                <div class="below-ceil col-md-6 col-sm-6">
                    <i class='glyphicon glyphicon-leaf'></i>
                    
                </div>
            </div>

        </div>
        
    </div>
</template>


<script>
import axios from 'axios'
//  login 
axios.defaults.withCredentials = true

import store from '@/vuex/index'
import {mapState, mapMutations, mapGetters, mapActions} from 'vuex'


export default {
    store,
    data () {
        return {
            msg: ' main ',
        }
    },
    methods: {
        ...mapMutations(['get_index']),
        go(path) {
            this.$router.push({
                name: path,
            })
            const dicta = {
                'TopicIndex': 0,
                'Hotspot': 1,
                'Photo': 2,
            }
            const to_index = dicta[path]
            this.$emit('go_header', to_index)
        }
        
    },
    computed: {
        ...mapState(['username', 'index_link']),
    },
    created() {
        this.get_index()
    },

}
</script>

<style scoped>
@import "../../../static/css/index/demo.css";
</style>





