<template>
    <div>
        {{  msg  }}
            <h4></h4>

            <div class="input-group">
                username: {{ username }}
                password: {{ password }}
                <form action="/user/register" method="post" onsubmit='return false'>
                    <input type="input" class="form-control" v-model='username' name='username' placeholder="">
                    <br>
                    <input type="input" class="form-control" v-model='password' name='password' placeholder="">
                    <br>
                    <button class="btn btn-info" @click='registMethod'></button>
                </form>
                {{ infor }}
                <br>
                <router-link to="/user/login"></router-link>
            </div>
    </div>
</template>

<script>

import axios from 'axios'

export default {
    data () {
        return {
            msg: ' user register ',
            api_link: `${domain}/user`,
            api_link_regist: `${domain}/user/register`,
            infor: '',
            username: '',
            password: '',
            infor: '',
            domain: `${domain}`,
            
        }
    },
    methods: {
        get_infor() {
            axios.get(this.api_link).then(res => {
                // log(':', res)
            }).catch(res => {
                log('')
            })
        },
        registMethod() {
            // log('registMethod ')
            axios.post(this.api_link_regist, {
                username: this.username,
                password: this.password,
            }).then(res => {
                // log(', :', res.data.infor)
                this.infor = res.data.infor
                this.username = ''
                this.password = ''
            }).catch(res => {
                log('')
            })
        }
    },
    created() {
        // log('hahah')
        this.get_infor()

    }
}

</script>

<style scoped>

</style>





