<template>
    <div>
        <div class="add-main">
            <div class="add">
                <input class='add-input' v-model='new_board' type="input" name="board_content" placeholder="">
                <button class="btn-add" @click='addMethod'></button>
            </div>
        </div>
        <ul>
        <div class="load">
            <div v-for="(board, index) in boards" class="load-cell" :key="index">
                <li>{{ board.board_content }}
                    <a class="remove-main" data-id="board._id" @click='deleteMethod(board._id)'></a>
                    <!-- <a href="/board/edit?board_id=board._id" @click='editMethod(board._id)'></a> -->
                    <router-link :to="{name: 'BoardEdit', query: {board_id: board._id}}"></router-link></li>
                <br>
            </div>
        </div>

        </ul>
        <router-link to="/topic"></router-link>

    </div>
</template>


<script>
import axios from 'axios'

export default {
    data () {
        return {
            msg: ' board admin ',
            board_link: `${domain}/board`,
            delete_link: `${domain}/board/api/delete?board_id=`,
            add_link: `${domain}/board/api/add`,
            boards: '',
            new_board: '',
        }
    },
    methods: {
        get_board() {
            // log('get_board')
            axios.get(this.board_link).then(res => {
                // log(' board admin ! :', res.data)
                this.boards = res.data.boards

            }).catch(res => {
                log(' board admin  ')
            })
        },
        delete_board(board_id) {
            axios.get(this.delete_link + board_id).then(res => {
                // log(' delete_board  ! :', res.data)
                this.boards = res.data

            }).catch(res => {
                log('delete_board ')
            })
        },
        add_board() {
            axios.post(this.add_link, {
                board_content: this.new_board
            }).then(res => {
                this.boards = res.data

            }).catch(res => {
                log(' add_board ')
            })
        },
        deleteMethod(board_id) {
            // log('', board_id)
            this.delete_board(board_id)
        },
        addMethod() {
            // log('')
            this.add_board()
        },
    },
    created() {
        this.get_board()
    }
  
}
</script>

<style scoped>

</style>





